import React, { useEffect } from 'react';
import { experienceItems } from './experienceItems';
import { Grid } from '@mui/material/';
import { useState } from 'react';

function importAll(r) {
    let images = {};
    r.keys().map(item => { images[item.replace('./', '')] = r(item); });
    return images;
}
const images = importAll(require.context('../../assets', false, /\.png/));

const Experience = () => {
   

    
    return (
        <div id="experience-section" className="experienceDiv" >
            <div className="introDiv" style={{marginBottom:"15px"}}>
                 <h1>Where I've worked at</h1>                 
           </div>    
           <div style={{marginLeft:"20vw"}}>
                {experienceItems.map((item,indexOne)=>{
                    return <Grid key={indexOne} container>
                        <Grid item xl={1} lg={1} md={1} sm={12} xs = {12}>
                            <img key={indexOne}  style={{marginTop:"0px",width:"70px",marginRight:"0px"}} src={images[item.imageURL]} alt="img"></img>
                        </Grid>
                        <Grid item xl={11} lg={11} md={11} sm={12} xs = {12}>
                            <h2 >{item.name}</h2>
                            {/* <p style={{marginBottom:"10px"}}>{totalLength(duration)}</p> */}
                            {item.items.map((job,indexTwo)=>{

                                return <div style={{marginTop:"20px"}} key={indexTwo}>
                                    <h3>{job.position}</h3>
                                    <p style={{marginTop:"4px"}}>{job.role}</p>
                                    <p className='jobDuration'>{job.startDate} - {job.endDate}  </p>
                                    <div style={{marginLeft:"0px"}}>
                                        <ul>
                                            {job.bulletPoints.map((point,indexThree)=>{
                                               return <li style={{marginBottom:"6px"}} className="experiencePointers" key={indexThree}>{point}</li> 
                                            })}
                                        </ul>
                                    </div>
                                </div>
                            })}
                        </Grid>
                    </Grid>
                })}
           </div>
        </div>

    );
};

export default Experience;