import React,{useState,useEffect} from 'react'



function Hero(){
    const [intialLoad,setLoad] = useState(true)
    useEffect(()=>{
        setLoad(false)
    },[])

    

    const easterText= 'Oops i dont think this is suppose to be here \nClick anywhere on the screen for a change of view'
    let newText = easterText.split('\n').map(i => {
      
        if(i[0] === 'C'){
            const endOfAnywhere = i.indexOf("anywhere") + 8
            return <p key={i[0]}> <span  className="blueGradient easterText clickAnywhere">{i.slice(0,endOfAnywhere)} </span> {i.slice(endOfAnywhere)} </p>
        }else{
            return <p style={{textAlign:"center"}}  key={i[0]} className ="easter easterText">{i}</p>
        }
        
    });
    const [image,setImage] = useState(140)
    const [easter,setEaster] =useState(newText)

    
    function importAll(r) {
        let images = {};
        r.keys().map(item => {return images[item.replace('./', '')] =  r(item); });
        return images;
    }
    const images = importAll(require.context('../../assets', false, /\.jpeg/));
    const img = `pic${image}.jpeg`
    function changePic(){
        const num =Math.floor(Math.random() * 112 +140)
        setImage(num)
        if (num === 140){
            setEaster('why is this still here? There must be a glitch.')
        }else{
            setEaster("")
        }
        console.log(num)
    }
    const style ={ranHero:{
            backgroundImage:`url(${images[img]})`
        }
    }
    return (
        <div 
        style = {style.ranHero}
        onClick={()=>{
            changePic()
        }} className="Hero" id ="home-section">

        <div className="overlay">
            <h1 >Hi I'm Ryan.</h1>
            <p>An aspiring software engineer</p>
            <div className='easterText'  style={{marginTop:"30px"}}>
                {easter}
            </div>
             {/* <p className ="easter" >{easter}</p>         */}
        </div>
       

        </div>
    )
}

export default Hero