import React from 'react'
import Profile from '../assets/Profile.JPG'
import Grid from '@mui/material/Grid';

function About(){
    const firstLine = "I am currently a first year computer engineering student at the National University of Singapore and would be graduating in the year 2026."
    const secondLine = "I started learning about programming during my National Service and would love to continue exploring new part time opportunities throughout my time in university."
    return(
        <div className="about" id="about-section">

            <h1 >About Me</h1>

        <Grid container spacing={4}>
            <Grid item md={3} xs={12}><img className="profile" src={Profile} alt="Profile"/> </Grid>
            <Grid item md={7} xs={12}><p className="profile-desc">{firstLine} </p> <p className="profile-desc">{secondLine}</p></Grid>
            
            </Grid>
        </div>
    )

}

export default About 