import Slider from "react-slick";
import { projectItems } from './projectsItem';
import { Button } from '@mui/material';
import Popup from "../Popup/Popup";
import { useEffect, useState } from "react";
import { WindowTwoTone } from "@mui/icons-material";
import Grid from "@mui/material/Grid";
import GitHubIcon from '@mui/icons-material/GitHub';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';


function importAll(r) {
    let images = {};
    r.keys().map(item => { images[item.replace('./', '')] = r(item); });
    return images;
}
const images = importAll(require.context('../../assets', false, /\.png/));


function SimpleSlider() {
    const [popup,togglePopup] = useState(false)
    const [project,setProject]= useState({})
    const [slidesToShow,setSlides] = useState(2)
    const [imagesToShow,setImages] = useState(3)
    const [popupImages,setPopupImages] = useState(2)

    const popupLeftRatio = 7
    const popupRightRatio = 12 - popupLeftRatio
    const [isModalOpen,setModal] = useState(false)

    useEffect(() =>{
        if(isModalOpen){
            document.body.style.overflow = "hidden";
        }else{
            document.body.style.overflow = "auto";
        }

       
    },[isModalOpen])

    const monitorWidth = ()=>{

        if( window.innerWidth < 1363){
            setSlides(1)
        }else{
            setSlides(2)
        }

        if(window.innerWidth > 480 && window.innerWidth < 694){
            setPopupImages(2)
            setImages(2)
        }else if(window.innerWidth< 480){
            setImages(1)
            setPopupImages(1)
        }else{
            setImages(3)
            setPopupImages(2)
        }



        
    }
    useEffect(()=>{
        monitorWidth()
        window.addEventListener("resize", monitorWidth)
    })
    var settings = {
      dots: true,
      infinite: true,
      speed: 1200,
      slidesToShow: slidesToShow,
      slidesToScroll: 1,
      autoplay:true,
      arrows:false,
      pauseOnHover:true,
      pauseOnFocus:true,
      autoplaySpeed:4000

    };
    return (
        <div>
      <Slider className='slider'  {...settings}>
        {projectItems.map((project,index) => {
            return <div style={{overflow:"auto"}}key={index}>

            {project.images.slice(0,imagesToShow).map((image,index)=>{
                     return (
                         <img key={index} className={project.class} src={images[image]} alt="img"></img>
                         )
                 })}
                        {/* <img className='desktop' src={images[project.images[0]]} alt="hi"></img>  */}
                        <h2>{project.title}</h2>
                        <p   style={{marginTop:"7px",marginBottom:"3px"}}>{project.for}</p>
                        <div style={{display:"block"}}>
                        {project.tech.map((tech,index) =>{
                        return (
                            <p className="tech-item" style={{display:"inline-block",paddingRight:"14px"}} key ={index}>{tech}</p>)
                        })}
                        </div>
                        <Button className='moreInfoButton' variant='outlined' 
                            onClick= {()=>{togglePopup(true) 
                            setModal(true)
                            setProject(project)}
                        }>More Info</Button>
                   </div>
        })}
        

      </Slider>
      {popup?
        <Popup modal={setModal} trigger={togglePopup}>
        <Grid style={{paddingLeft:"10px"}} container>
            <Grid item lg = {popupLeftRatio} xl={popupLeftRatio} md={12} sm={12} xs={12}>
                <h3  style={{paddingTop:'22px',paddingBottom:"22px"}} >{project.title}</h3>
                <p style={{paddingBottom:"22px"}} >{project.description}</p>
                <h4 style={{paddingBottom:"4px"}} >Technologies:</h4>
                <div style={{display:"block"}}>
                {project.tech.map((tech,index) =>{
                        return (
                            <p className="tech-item" style={{display:"inline-block",paddingRight:"14px"}} key ={index}>{tech}</p>)
                })}
                </div>
                {project.isLive?<Button className='sourceCodeButton' variant='outlined' style={{marginRight:"30px"}} endIcon={<OpenInNewIcon/>} onClick = {() =>{ window.open(project.live, '_blank')}}>Live Version</Button>:<div></div>}
                {project.isGithub?<Button className='sourceCodeButton' variant='outlined' endIcon={<GitHubIcon/>} onClick = {() =>{ window.open(project.github, '_blank')}}>Source Code</Button>:<div></div>} 
            </Grid>
            <Grid className="popupImageDiv" style={{paddingLeft:"42px"}} item lg = {popupRightRatio} xl={popupRightRatio} md={12} sm={12} xs={12}>
                {project.images.slice(0,popupImages).map((image,index)=>{
                     return (
                         <img key={index}  style={{height:"170px"}} src={images[image]} alt="img"></img>
                         )
                })}
            </Grid>
        </Grid>
        </Popup>:
        <div></div>}
      </div>
    );
  }

  export default SimpleSlider;